  .checkout2:hover
 {
   background-color: limegreen!important;
   color: white!important;
   box-shadow: 0 5px 5px  grey;
   transition: 0.3s linear;
 }
 .rmv2:hover{
  background-color: red;
  color: white;
  transition: .3s linear;
 }
 .adm2:hover{
  background-color: green;
  color: white;
  transition: .3s linear;
}
.checkout:hover
{
  background-color: limegreen!important;
  color: white!important;
